ary;\n                    }\n\n                    &.avatar {\n                        padding-right: 6px;\n                        padding-left: 2px;\n                        background: transparent;\n\n                        img {\n                            height: 28px;\n                            width: auto;\n                            margin-right: 5px;\n                        }\n\n                        span {\n                            color: $text-color;\n                            font-size: 10px;\n                            font-weight: 500;\n                            text-transform: uppercase;\n                        }\n                    }\n\n                    i {\n                        font-size: 18px;\n                    }\n                }\n\n                .ivu-badge {\n                    position: relative;\n\n                    a {\n                        display: block;\n                        max-height: 32px;\n                        width: 32px;;\n                        font-size: 11px;\n                        cursor: pointer;\n                        font-weight: 500;\n                        transition: all .2s ease-in-out;\n                        text-align: center;\n                        padding: 0;\n\n                        i,\n                        span {\n                            color: $text-color;\n                            line-height: 32px;\n                        }\n\n                        &:hover {\n                            i {\n                                color: $primary;\n                            }\n                        }\n                    }\n\n                    sup {\n                        top: 5px;\n                        right: 5px;\n                        box-shadow: none;\n                    }\n                }\n            }\n        }\n\n        > li {\n            position: relative;\n            float: left;\n\n            > a,\n            .ivu-poptip-re